import React from 'react'
import "./Contact.css"
import Phone from "../../Img/phone.jpg"
import Email from "../../Img/mail.png"
import Address from "../../Img/address.png"
import { useRef, useState } from "react"
import emailjs from "emailjs-com"



const Contact = () => {
    const formRef = useRef()
    const [done, setDone] = useState(false)



    const handleSubmit = (e) => {
        e.preventDefault()
        emailjs.sendForm('service_brhc05a', 'template_ulpc9h9', formRef.current, 'user_V22ICKzCGe2uVzkYspZCJ')
            .then((result) => {
                console.log(result.text);
                setDone(true)
            }, (error) => {
                console.log(error.text);
            });

    }




    return (
        <div className="c">
            <div className="c-bg"></div>
            <div className="c-wrapper">
                <div className="c-left">
                    <h1 className="c-title">Contact Me!</h1>
                    <div className="c-info">
                        <div className="c-info-item">
                            <img src={Phone} alt="" className="c-icon" />
                            +49 1763461786
                        </div>
                        <div className="c-info-item">
                            <img src={Email} alt="" className="c-icon" />
                            gakidisgiorgos@windowslive.com
                        </div>
                        <div className="c-info-item">
                            <img src={Address} alt="" className="c-icon" />
                            Grazer Damm 119, Berlin 12157, Germany
                        </div>
                    </div>
                </div>
                <div className="c-right">
                    <p className="c-desk">
                        <b>Are You Interested?</b>Get in touch with me if you want to work together!
                    </p>
                    <form ref={formRef} onSubmit={handleSubmit}  >
                        <input type="text" placeholder="Name" name="user_name" />
                        <input type="text" placeholder="Subject" name="user_subject" />
                        <input type="text" placeholder="Email" name="user_email" />
                        <textarea rows="5" placeholder="Message" name="message" />
                        <button>Submit</button>
                        {done && "Thank you..."}
                    </form>
                </div>
            </div>
        </div>
    )
}

export default Contact
